import {Component, Input, ChangeDetectionStrategy, HostBinding, ContentChild} from '@angular/core';
import {NglFormGroup} from './group';
import {NglFormLabelTemplate} from '../form-label';

@Component({
  selector: 'fieldset[ngl-form-group-alt]',
  template: `
    <legend class="slds-form-element__label"><abbr class="slds-required" *ngIf="required" title="required">*</abbr><span [nglInternalOutlet]="_label"></span></legend><div class="slds-form-element__control"><div [ngClass]="'slds-' + type + '--button-group'"><ng-content></ng-content></div></div><div class="slds-form-element__help" *ngIf="error">{{error}}</div>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.slds-form-element]': 'true',
  },
})
export class NglFormGroupAlternate extends NglFormGroup {

  @Input('label') labelStr: string;
  @ContentChild(NglFormLabelTemplate) labelTpl: NglFormLabelTemplate;

  @HostBinding('class.slds-has-error')
  @Input() error: string;

  @Input() required: boolean;

  @Input() type: string;

  // AoT workaround
  ngAfterContentInit() {
    super.ngAfterContentInit();
  }
};
